html {
  font-family: IRANSans, sans-serif;
}
body {
  font-family: IRANSans, sans-serif !important;
  font-size: 16px;
  font-weight: 300;
  color: #888;
  line-height: 30px;
  text-align: center;
  direction: rtl;
}
strong {
  font-weight: 500;
}
a,
a:hover,
a:focus {
  color: #19b9e7;
  text-decoration: none;
  -o-transition: all .3s;
  -moz-transition: all .3s;
  -webkit-transition: all .3s;
  -ms-transition: all .3s;
  transition: all .3s;
}
h1,
h2 {
  margin-top: 0;
  font-size: 32px;
  font-weight: 100;
  color: white;
  line-height: 50px;
}
h3 {
  font-size: 22px;
  font-weight: 300;
  color: #555;
  line-height: 30px;
}
img {
  max-width: 100%;
}
::-moz-selection {
  background: #19b9e7;
  color: white;
  text-shadow: none;
}
::selection {
  background: #19b9e7;
  color: white;
  text-shadow: none;
}
.btn-link {
  height: 50px;
  width: 150px;
  margin: 5px;
  padding: 16px 20px 0 20px;
  font-size: 16px;
  font-weight: 300;
  line-height: 16px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  text-decoration: none !important;
}
.btn-link.btn-link-github {
  background: #eee;
  color: #000;
}
.btn-link.btn-link-github:hover {
  background: #ccc;
}
.btn-link.btn-link-google {
  background: #dd4b39;
  color: white;
}
.btn-link.btn-link-google:hover {
  background: #ff4b39;
}
.btn-link i {
  padding-right: 5px;
  vertical-align: middle;
  font-size: 20px;
  line-height: 20px;
}
/***** Top content *****/
.inner-bg {
  padding: 30px 0 60px 0;
}
.top-content .text {
  color: white !important;
}
.form-box {
  margin-top: 30px;
}
.form-top {
  overflow: hidden;
  padding: 0 25px 10px 25px;
  background: white;
  -moz-border-radius: 4px 4px 0 0;
  -webkit-border-radius: 4px 4px 0 0;
  border-radius: 4px 4px 0 0;
  text-align: left;
}
.form-top-left {
  float: right;
  width: 75%;
  padding-top: 25px;
  text-align: right;
}
.form-top-left h3 {
  margin-top: 0;
}
.form-top-right {
  float: right;
  width: 25%;
  padding-top: 15px;
  font-size: 66px;
  color: #ddd;
  line-height: 100px;
  text-align: left;
}
.form-bottom {
  padding: 25px 25px 30px 25px;
  background: #eee;
  -moz-border-radius: 0 0 4px 4px;
  -webkit-border-radius: 0 0 4px 4px;
  border-radius: 0 0 4px 4px;
  text-align: left;
}
.form-bottom form textarea {
  height: 100px;
}
.form-bottom form button.btn {
  width: 100%;
}
.form-bottom form .input-error {
  border-color: #19b9e7;
}
.social-login {
  margin-top: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.social-login h3 {
  color: white;
}
.social-login-buttons {
  margin-top: 25px;
}
.middle-border {
  min-height: 300px;
  margin-top: 170px;
  border-right: 1px solid white;
  border-right: 1px solid rgba(255, 255, 255, 0.6);
}
/***** Media queries *****/
@media (max-width: 767px) {
  .middle-border {
    min-height: auto;
    margin: 65px 30px 0 30px;
    border-right: 0;
    border-top: 1px solid white;
    border-top: 1px solid rgba(255, 255, 255, 0.6);
  }
}
@media (max-width: 415px) {
  h1,
  h2 {
    font-size: 32px;
  }
}
.g-recaptcha {
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
